-webkit-transform:scale(1.03);transform:scale(1.03)}.get-btn:active{-webkit-transform:scale(0.97);transform:scale(0.97)}.joke-container{position:relative;list-style:none;min-height:200px;background:#F8F8F8;border-radius:20px;padding:40px;margin-bottom:20px}.joke-container .favorite-btn{position:absolute;top:40px;right:40px;border:none;outline:none;background-color:transparent;cursor:pointer;-webkit-appearance:none;-moz-appearance:none}.joke-container .flex{display:flex;align-items:flex-start;margin-top:30px}.joke-container .flex .joke-content{padding-left:20px;width:100%}.joke-container .flex .joke-content .id{font-weight:500;font-size:10px;line-height:14px;color:#ABABAB;margin-bottom:10px}.joke-container .flex .joke-content .id .id-link{color:#8EA7FF}.joke-container .flex .joke-content .id .link-icon{margin-left:5px}.joke-container .flex .joke-content .joke{font-size:18px;line-height:26px;color:#333333;margin-bottom:28px}.joke-container .flex .joke-content .joke-footer-flx{display:flex;justify-content:space-between;align-items:center}.joke-container .flex .joke-content .joke-footer-flx .update{font-size:10px;line-height:14px;color:#ABABAB}.joke-container .flex .joke-content .joke-footer-flx .update .update-b{font-weight:500}.joke-container .flex .joke-content .joke-footer-flx .ctgr-display{font-weight:500;font-size:10px;line-height:14px;letter-spacing:2px;text-transform:uppercase;color:#333333;background:#FFFFFF;border-radius:6px;padding:5px 20px}@media only screen and (max-width: 415px){.joke-container{padding:20px}.joke-container .favorite-btn{top:20px;right:20px}}*{box-sizing:border-box;margin:0;padding:0}.app-container{display:flex;font-family:'Roboto', sans-serif;color:#333333;height:100%}.main-container{position:relative;display:flex;width:100%;height:100%}.main-container .main-wrapper{width:680px;margin:auto;padding:20px;margin-top:0}.main-container .main-wrapper .logo{font-style:normal;font-weight:bold;font-size:20px;margin-bottom:78px;margin-top:5px}.main-